.ids-grid {
  width: 80px;
  height: 80px;
  z-index: 10000;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
}

.ids-grid div {
  width: 16px;
  height: 16px;
  background: #fa9212;
  border-radius: 50%;
  animation: 1.2s linear infinite lds-grid;
  position: absolute;
}

.ids-grid div:nth-child(1) {
  animation-delay: 0s;
  top: 8px;
  left: 8px;
}

.ids-grid div:nth-child(2) {
  animation-delay: -.4s;
  top: 8px;
  left: 32px;
}

.ids-grid div:nth-child(3) {
  animation-delay: -.8s;
  top: 8px;
  left: 56px;
}

.ids-grid div:nth-child(4) {
  animation-delay: -.4s;
  top: 32px;
  left: 8px;
}

.ids-grid div:nth-child(5) {
  animation-delay: -.8s;
  top: 32px;
  left: 32px;
}

.ids-grid div:nth-child(6) {
  animation-delay: -1.2s;
  top: 32px;
  left: 56px;
}

.ids-grid div:nth-child(7) {
  animation-delay: -.8s;
  top: 56px;
  left: 8px;
}

.ids-grid div:nth-child(8) {
  animation-delay: -1.2s;
  top: 56px;
  left: 32px;
}

.ids-grid div:nth-child(9) {
  animation-delay: -1.6s;
  top: 56px;
  left: 56px;
}

@keyframes lds-grid {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }
}

